@import url('https://fonts.googleapis.com/css?family=Homenaje&display=swap');

body
{
  width: 100%;
  height: 100%;
  background-image:url(images/Background_4blur.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  font-family: 'Homenaje', sans-serif;
  margin: 0;
  padding: 0;
}
#app{
  min-width: 100%;
  min-height: 100%;
}

.btn{
  background: #FB6549;
  color: #ecf0f1;
  width: 80%;
  padding: 3px;
  text-align: center;
  text-transform: uppercase;
  font-weight: bolder;
  margin-top:30px;
  margin-left: 10%;
  margin-right: 10%;
  letter-spacing:0.2em;
  font-size:16px;
  transition: all 0.3s;
  box-shadow: rgb(0 0 0 / 45%) 0px 2px 4px 0px;
}
.btn:hover{
  background: #FF4D41;
  cursor: pointer;
}

.btnAppleLogin{
  background: #000;
  color: #ecf0f1;
  width: 40px;
  height: 40px;
  min-height: 40px !important;
  border-radius: 25px;
  padding: 3px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  font-weight: bolder;
//margin-top:30px;
  margin-bottom: 30px;
  margin-right: 10%;
  letter-spacing:0.2em;
  font-size:16px;
  transition: all 0.3s;
  box-shadow: rgb(0 0 0 / 45%) 0px 2px 4px 0px;
}
.btnAppleLogin:hover{
  background: #FFF;
  cursor: pointer;
  color: black;
}
.btnAppleLogin:hover i{
  color: #fff;
}

.loginOptionText{
  color: white;
  align-self: center;
}

.userIDText{
  color: white;
  align-self: flex-end;
  align-items: start;
  margin-right: 20px;
}

.btnFacebookLogin{
  background: #3C5999;
  color: #ecf0f1;
  width: 40px;
  height: 40px;
  min-height: 40px !important;
  border-radius: 25px;
  padding: 3px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  font-weight: bolder;
  //margin-top:30px;
  margin-left: 30%;
  margin-right: 30%;
  margin-bottom: 30px;
  letter-spacing:0.2em;
  font-size:16px;
  transition: all 0.3s;
  box-shadow: rgb(0 0 0 / 45%) 0px 2px 4px 0px;
}
.btnFacebookLogin:hover{
  background: #FFF;
  cursor: pointer;
  color: black;
}
.btnFacebookLogin:hover i{
  color: #fff;
}
.otherLoginButtons{
  display: flex;
  flex-direction: row;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.box{
  width: 400px;
  height: 500px;
  background: rgba(47, 54, 64, 0.5);
  margin: 0 auto;
  border-radius:12px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  padding-bottom:20px;
  padding-top:20px;
}

.profileBox{
  width: 400px;
  height: 400px;
  background: rgba(47, 54, 64, 0.5);
  margin: 0 auto;
  border-radius:12px;
  display: flex;
  flex-direction: column;
  padding-bottom:20px;
  padding-top:20px;
}
.box h3{
  color: #ecf0f1;
  margin-left: 10%;
  text-transform: uppercase;
  font-size: 12px;
  letter-spacing: 0.2em;
}

.loginContainer{
  min-height:100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.metamaskIcon{
  width: 30px;
  height: 30px;
}
.walletName{
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.walletConnectBox{
  width: 400px;
  height: 300px;
  background: rgba(47, 54, 64, 0.5);
  margin: 0 auto;
  border-radius:12px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  padding-bottom:20px;
  padding-top:20px;
}

.btnMetamaskConnect{
  background: #FB6549;
  color: #ecf0f1;
  width: 80%;
  padding: 3px;
  text-align: center;
  text-transform: uppercase;
  font-weight: bolder;
  margin-top:30px;
  margin-left: 10%;
  margin-right: 10%;
  letter-spacing:0.2em;
  font-size:16px;
  transition: all 0.3s;
  box-shadow: rgb(0 0 0 / 45%) 0px 2px 4px 0px;
}

.btnMetamaskConnect:hover{
  background: #FF4D41;
  cursor: pointer;
}

.btnDepositAndWithdraw{
  background: #FB6549;
  color: #ecf0f1;
  width: 120px !important;
  height: 50px;
  padding: 3px;
  text-align: center;
  text-transform: uppercase;
  font-weight: bolder;
  margin-top:30px;
  letter-spacing:0.2em;
  font-size:16px;
  transition: all 0.3s;
  box-shadow: rgb(0 0 0 / 45%) 0px 2px 4px 0px;
}

.btnDepositAndWithdraw:hover{
  background: #FF4D41;
  cursor: pointer;
}

.input{
  font-family:inherit;
  background:#ecf0f1;
  margin-left: 10%;
  margin-right: 10%;
  width: 75%;
  padding-top:10px;
  padding-bottom:10px;
  padding-left:10px;
  padding-right:10px;
  border: 1px solid #DDDDDD;
  outline:none;
  transition: all 0.30s ease-in-out;
  letter-spacing:0.15em;
}
.inputFieldLabel{
  color: white;
  margin-left: 10%;
}
.input:focus{
  box-shadow: 0 0 5px rgba(251, 101, 73, 1);
  border: 1px solid rgba(255, 77, 65, 1);
}

.errorMsg h3, .hideErrorMsg h3{
  color: #efefef;
  font-weight:700;
  letter-spacing: 0.27em;
  background: rgba(255, 77, 65, 0.5);
  width:75%;
  padding:10px;
}
.errorMsg h3{
  animation: slideIn 0.3s cubic-bezier(0.165, 0.84, 0.44, 1) forwards;
}

.hideErrorMsg h3{
  animation: slideOut 0.9s cubic-bezier(0.165, 0.84, 0.44, 1) forwards;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translate3d(-20rem, 0, 500rem);
  }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

@keyframes slideOut {
  from {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
  to {
    opacity: 0;
    transform: translate3d(-20rem, 0, 500rem);
  }
}